@import url(https://fonts.googleapis.com/css2?family=Oswald:wght@200;500&display=swap);
.hide {
    display: none;
}

.show {
    display: block;
}

.main-title {
    top: 10px;
    position: absolute;
    font-size: xx-large;
}

.formBg {
    padding: 10px;
    background-color: lightgray;
    margin-bottom: 30px;
    height: 40px;
    margin-top: 60px;
}

.nav {
    position: relative;
    margin: auto;
    display: inline-block;
}

.button {
    background-color: darkgrey;
    color: white;
    outline: none;
    height: 30px;
    margin-top: 5px;
    margin-right: 20px;
    font-weight: bold;
}

.button:hover {
    color: black;
}

.input {
    width: -webkit-max-content;
    width: -moz-max-content;
    width: max-content;
    border: 1px solid transparent;
    background-color: transparent;
    cursor: pointer;
    height: 50px;
}

.searchButton {
    border: 1px solid transparent;
    background-color: white;
    color: grey;
    font-weight: bold;
    cursor: pointer;
}

.fliter {
    float: right;
    border: 1px solid white;
    padding: 5px;
}

.fliter input  {
    margin: 0px;
    margin-right: 10px;
    height: 24px;
    background-color: white;
}

@media only screen and (max-width: 600px){
    .input, .searchButton {
        align-items: center;
    }
    
    .searchButton:hover {
        border: 1px solid black;
        background-color: transparent;
        color: black;
    }

    .main-title {
        position: absolute;
        left: 0px;
        width: 100%;
        text-align: center;
    }

    .formBg {
        width: 95%;
        margin-top: 100px;
        height: 80px;
    }

    .button {
        margin-right: 5px;
        font-weight: normal;
    }

    .fliter {
        border: 1px solid white;
        text-align: center;
        float: none;
        margin: 0px;
    }

    .nav {
        display: block;
        text-align: center;
    }

    .nav button:last-child {
        margin-right: 0px;
    }
}
